@media only screen and (min-width: 700px) { .page-header .page-title-text { font-size: 36px; } }

@media only screen and (min-width: 1000px) { .page-header .page-title-text { font-size: 65px; } }

.page-header .page-title { display: flex; flex: 1; justify-content: center; align-items: center; position: relative; z-index: 10; text-transform: uppercase; }

.page-header .page-title:before { position: absolute; right: 0; left: 0; top: 0; bottom: 0; margin: auto; content: ''; width: 50px; height: 100px; transform: skewX(-22deg); background-color: #0181e4; z-index: -1; }

@media only screen and (min-width: 500px) { .page-header .page-title:before { width: 70px; height: 120px; } }

@media only screen and (min-width: 700px) { .page-header .page-title:before { width: 100px; width-height: 200px; } }

@media only screen and (min-width: 1000px) { .page-header .page-title:before { width: 130px; height: 250px; } }

.page-header .main-logo { display: none; margin-bottom: -60px; }

@media only screen and (min-width: 1000px) { .page-header .main-logo { display: block; } }

@media only screen and (min-width: 1000px) { .page-header .main-logo .logo-text { font-size: 17px; } }

.breadcrumb { display: flex; flex-wrap: wrap; padding: 16px 0; justify-content: center; }

@media only screen and (min-width: 500px) { .breadcrumb { margin-top: 30px; margin-bottom: 10px; } }

@media only screen and (min-width: 700px) { .breadcrumb { margin-top: 50px; margin-bottom: 30px; } }

@media only screen and (min-width: 1000px) { .breadcrumb { justify-content: flex-start; margin-top: 60px; margin-bottom: 50px; } }

.breadcrumb-item a { font-size: 14px; color: #757575; }

.breadcrumb-item.active { color: #000000; }

.breadcrumb-item { font-size: 14px; }

.breadcrumb-item + .breadcrumb-item::before { display: inline-block; padding-right: .5rem; padding-left: .5rem; color: #000000; content: ">"; }

.page-services-list { margin: 0 -10px; }

@media only screen and (min-width: 700px) { .page-services-list { display: flex; flex-wrap: wrap; } }

@media only screen and (min-width: 1000px) { .page-services-list { margin-bottom: 150px; margin: 0 -15px; } }

.page-services-item { padding: 0 10px; margin: auto; margin-bottom: 10px; height: 450px; max-width: 450px; }

@media only screen and (min-width: 600px) { .page-services-item { margin-bottom: 20px; } }

@media only screen and (min-width: 700px) { .page-services-item { flex: 0 0 50%; max-width: 50%; margin-bottom: 30px; } }

@media only screen and (min-width: 1000px) { .page-services-item { flex: 0 0 33%; max-width: 33%; padding: 0 15px; max-width: 335px; } }

@media only screen and (min-width: 1200px) { .page-services-item { flex: 0 0 25%; max-width: 25%; } }

.page-services-link { display: flex; flex: 1; height: 100%; position: relative; background-color: #83888e; }

.page-services-link img { width: 100%; height: 100%; object-fit: cover; }

.page-services-link:before { position: absolute; content: ''; left: 0; right: 0; top: 0; bottom: 0; margin: auto; background: linear-gradient(rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.9)); }

.page-services-title { text-transform: uppercase; padding: 0 50px; color: #fff; font-size: 14px; font-weight: 700; text-align: center; position: absolute; left: 0; right: 0; bottom: 25px; }

.page-services-item-last { display: flex; flex-direction: column; text-align: center; justify-content: space-around; flex: 1; padding: 50px 40px; }

.page-services-last-title { font-size: 18px; color: #000; }

@media only screen and (min-width: 1000px) { .page-services-last-title { font-size: 29px; } }

.page-services-help-text { font-size: 13px; font-weight: 700; color: #000; }

@media only screen and (min-width: 1000px) { .page-services-help-text { font-size: 14px; } }

.contacts { text-align: center; margin: 0 -10px; }

@media only screen and (min-width: 800px) { .contacts { display: flex; } }

@media only screen and (min-width: 1000px) { .contacts { margin: 0 -15px; } }

.contacts-bg { background: url("../img/contacts-bg.png"); background-repeat: no-repeat; background-position: 0 50px; background-size: contain; }

@media only screen and (min-width: 600px) { .contacts-bg { background-position: center 100px; background-size: contain; } }

.contacts-item { margin-bottom: 30px; padding: 0 15px; }

@media only screen and (min-width: 1000px) { .contacts-item { margin-bottom: 150px; } }

@media only screen and (min-width: 800px) { .contacts-item-3 { flex: 3; } }

@media only screen and (min-width: 800px) { .contacts-item-4 { flex: 4; } }

.contacts-title { text-transform: uppercase; font-size: 18px; color: #0a0a0a; font-weight: 900; margin-bottom: 15px; }

@media only screen and (min-width: 1000px) { .contacts-title { text-align: left; margin-bottom: 20px; } }

.contacts-list { text-align: left; }

