.contacts-list-item { font-size: 13px; margin-bottom: 30px; position: relative; }

.contacts-list-item:before { position: absolute; left: 3px; bottom: -2px; width: 11px; height: 11px; background-color: #ff4f4f; content: ''; margin: auto; border-radius: 50%; z-index: -1; }

