.contacts-social { margin-bottom: 20px; }

@media only screen and (min-width: 1000px) { .contacts-social { margin-bottom: 70px; } }

.contacts-social-title { font-size: 13px; text-transform: uppercase; color: #0f0f0f; font-weight: 700; margin-bottom: 10px; }

