@media only screen and (min-width: 1000px) { .contacts-feedback-title { font-size: 24px; } }

.contacts-feedback-help { display: inline-block; font-size: 13px; color: #2b2b2b; font-weight: 700; margin-bottom: 20px; }

@media only screen and (min-width: 1000px) { .contacts-feedback-help { font-size: 16px; margin-bottom: 30px; } }

