.main-header {
    background-color: #3b496d;
    z-index: 10;
    text-align: center;
    color: $white;
    position: relative;
    min-height: 480px;
    @include q(1000) {
        min-height: 833px; } }

.main-header-slider {
    overflow: hidden;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    z-index: -1; }

.main-header-slide {
    height: 100%;
    background-repeat: no-repeat;
    background-position: center 57px;
    background-size: cover;
    // padding-bottom: 90.76086956521739%
    min-height: 480px;
    @include q(1000) {
        background-position: top center;
        min-height: 833px; } }

// .main-header-slider .slick-list,
// .main-header-slider .slick-track
//     height: 100%


.header-block {
    display: flex;
    flex-direction: column; }

.header-btn {
    order: -1;
    z-index: 100;
    padding: 10px 0px;
    padding-top: 13px;
    align-self: center;
    margin-left: 25px;
    @include q(1000) {
        position: absolute;
        right: 37px;
        top: 60px;
        padding: 0; } }

.call-btn {
    border: 1px solid #000;
    @include q(1000) {
        border-color: #ffffff;
        color: #fff; } }

.menu-btn {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    background-color: $white;
    min-height: 57px;
    z-index: 100;
    @include q(1000) {
        right: auto;
        bottom: 0;
        min-height: auto;
        min-width: 55px;
        background-color: #f2f2f2; }
    @include q(1200) {
        min-width: 105px; }

    .icon-menu {
        position: absolute;
        width: 25px;
        height: 25px;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        margin-left: 10px;
        box-sizing: border-box;
        cursor: pointer;
        @include q(1000) {
            top: 60px;
            left: 0;
            right: 0;
            margin: auto;
            bottom: auto; } } }

