.main-logo {
    padding-top: 37px;
    margin-bottom: 65px;
    @include q(1000) {
        padding-top: 60px; } }

.logo {
    &-text {
        font-size: 12px;
        font-weight: 400;
        letter-spacing: -0.09em;
        text-transform: uppercase;
        transform: scaleY(1.4);
        text-transform: uppercase;
        display: inline-block;
        vertical-align: middle;
        @include q(1000) {
            font-size: 19px; } }
    &-img {
        width: 25px;
        height: 22px;
        display: inline-block;
        vertical-align: middle;
        @include q(1000) {
            width: 38px;
            height: 33px; } } }

.header-map {
    display: none;
    margin-bottom: 60px;
    @include q(1000) {
        display: inline-block; } }

.main-title {
    font-size: 28px;
    text-transform: uppercase;
    margin-bottom: 23px;
    font-weight: 900;
    line-height: 1.2;
    @include q(500) {
        font-size: 38px; }
    @include q(1000) {
        font-size: 50px; } }

.hero {
    padding-bottom: 40px; }

.hero-text {
    font-size: 14px;
    padding: 0 15px;
    line-height: 1.5;
    max-width: 740px;
    margin: auto;
    margin-bottom: 35px;
    @include q(500) {
        font-size: 18px; }
    @include q(700) {
        font-size: 24px; }
    @include q(1000) {
        margin-bottom: 20px; } }

.header-social {
    display: none;
    background-color: #fff;
    position: absolute;
    bottom: 0;
    right: 0;
    color: $black;
    font-weight: 700;
    text-transform: uppercase;
    @include q(1000) {
        display: block; } }

.header-next {
    display: none;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    @include q(1500) {
        display: block; } }

.header-next .btn-next {
    color: #fff;
    padding: 34px 30px;
    font-size: 10px;
    position: relative;
    padding-left: 50px;
    &:before {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        background-color: #fff;
        content: '';
        width: 29px;
        height: 1px; } }



.header-social-item {
    display: inline-block;
    padding: 24px 20px;
    font-size: 14px;
    @include q(1200) {
        padding: 34px 30px; } }

.header-social-link {
    display: block;
    color: $black;
    font-weight: 700; }


.main-nav {
    position: absolute;
    background-color: $blue;
    padding-top: 57px;
    left: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    padding-right: 10px;
    z-index: 99;
    transition: .5s all ease-in-out;
    transform: translateX(-200%);
    min-height: 480px;
    @include q(1000) {
        min-width: 500px;
        left: 55px;
        bottom: 0; }
    @include q(1200) {
        left: 105px; } }

.main-nav.active {
    transform: translateX(0%); }


.nav-logo {
    margin-top: 50px;
    margin-bottom: 70px; }

.nav-list {
    text-align: left;
    margin-bottom: 20px; }

.nav-item {
    margin-bottom: 25px; }

.nav-item:last-child {
    margin-bottom: 0; }

.nav-link {
    color: $white;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 13px;
    display: block;
    padding-left: 43px;
    @include q(1000) {
        font-size: 30px;
        padding-left: 130px; } }

.nav-link.active {
    position: relative;
    font-weight: 900;
    font-size: 16px;
    @include q(1000) {
        font-size: 40px; }
    &:before {
        position: absolute;
        content: '';
        background-color: $white;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        width: 23px;
        height: 1px;
        @include q(1000) {
            left: -10px;
            width: 120px;
            height: 2px; } } }



.nav-social {
    padding-top: 20px;
    border-top: 1px solid #1087e2;
    margin-top: auto;
    @include q(1000) {
        border-top: none; } }

.nav-social-list {
    @include q(1000) {
        display: flex;
        justify-content: space-around; } }

.nav-social-link {
    color: $white;
    text-transform: uppercase;
    margin-bottom: 12px;
    display: block;
    @include q(1000) {
        margin-bottom: 40px; } }





