.particle-container {
	@include r(800) {
		display: none; } }

.particle {
	position: absolute;
	background: $white;
	box-shadow: 0px 16px 9px 0px rgba(255, 255, 255, 0.66); }

.particle.red {
	background: $red;
	box-shadow: 0px 16px 9px 0px rgba(253, 43, 43, 0.66); }




.particle.blue {
	background: $blue;
	box-shadow: 0px 16px 9px 0px rgba(1, 129, 228, 0.66); }


@for $i from 1 through 5 {
	@keyframes particle-animation-#{$i} {
		100% {
			transform: translate3d((random(90) * 1vw), (random(90) * 1vh), (random(100) * 1px)) skewX(25deg) rotateX((random(360) * 1deg)) rotateZ((random(360) * 1deg)); } }

	.particle:nth-child(#{$i}) {
		animation: particle-animation-#{$i} 45s infinite;
		animation-direction: alternate;
		// animation-fill-mode: forwards
		$size: random(5) + 7;
		opacity: random(100)/100 + 0.4;
		height: $size + px;
		width: $size * 2 + px;
		animation-delay: -$i * .2s;
		transform: translate3d((random(90) * 1vw), (random(90) * 1vh), (random(100) * 1px)) skewX(25deg) rotateX(40deg); }

	.feedbacks .particle:nth-child(#{$i}) {
		animation-delay: $i * 100s; }

	.certification .particle:nth-child(#{$i}) {
		animation-delay: -$i * 20s;
		animation-duration: 30s; }

	.about .particle:nth-child(#{$i}) {
		animation-delay: -$i * 20s;
		animation-direction: alternate; } }

